"use client";

import { useSession, signIn } from "next-auth/react";
import Link from "next/link";
import { Button } from "@/components/ui/button";

export default function Home() {
  const { data: session, status } = useSession();

  if (status === "loading") {
    return (
      <div className="flex h-screen items-center justify-center bg-[#F8FAFC]">
        <p className="text-lg text-[#64748B]">Cargando...</p>
      </div>
    );
  }

  return (
    <div className="min-h-screen bg-[#F8FAFC] flex items-center justify-center p-6">
      <div className="w-full max-w-md">
        <div className="bg-white rounded-xl shadow-lg border border-gray-200 p-8">
          {/* Logo/Header */}
          <div className="text-center mb-8">
            <h1 className="text-3xl font-bold text-[#0F172A] mb-2">Scena</h1>
            <p className="text-[#64748B]">Gestión de Proyectos Visuales</p>
          </div>

          {session ? (
            <>
              <div className="text-center mb-6">
                <h2 className="text-2xl font-semibold text-[#0F172A] mb-2">
                  Bienvenido de nuevo
                </h2>
                <p className="text-[#64748B]">
                  {session.user?.name || session.user?.email}
                </p>
              </div>
              <Link href="/dashboard" passHref className="block">
                <Button size="lg" className="w-full bg-[#2563EB] hover:bg-[#1D4ED8] text-white">
                  Ir al Dashboard
                </Button>
              </Link>
            </>
          ) : (
            <>
              <div className="text-center mb-6">
                <h2 className="text-2xl font-semibold text-[#0F172A] mb-3">
                  Comienza a gestionar tus proyectos
                </h2>
                <p className="text-[#64748B] leading-relaxed">
                  La herramienta definitiva para el seguimiento colaborativo de proyectos.
                </p>
              </div>

              <div className="space-y-3">
                <Button
                  size="lg"
                  className="w-full bg-[#2563EB] hover:bg-[#1D4ED8] text-white"
                  onClick={() => signIn()}
                >
                  Iniciar Sesión
                </Button>

                <Link href="/register" passHref className="block">
                  <Button
                    size="lg"
                    variant="outline"
                    className="w-full border-[#CBD5E1] text-[#0F172A] hover:bg-[#F1F5F9]"
                  >
                    Crear una Cuenta
                  </Button>
                </Link>
              </div>
            </>
          )}
        </div>

        {/* Footer text */}
        <p className="text-center mt-6 text-sm text-[#64748B]">
          Simplifica la complejidad de la producción visual
        </p>
      </div>
    </div>
  );
}
